import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import SignUpDiv from "../styles/SignUpCss";
// firebase   
import firebase from "../firebase";
import axios from "axios";
// user  
import { useSelector } from "react-redux";
import { loginUser, clearUser } from "../reducer/userSlice";

const UserInfo = () => {
  //       
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);
  const fireUser = firebase.auth();

  const [email, setEmail] = useState("");
  const [pw, setPw] = useState("");
  const [pwCheck, setPwCheck] = useState("");

  useEffect(() => {
    setNickName(user.nickName);
    setEmail(user.email);
    setPw("");
    setPwCheck("");
  }, []);

  //   
  const [btFlag, setBtFlag] = useState(false);

  const navigate = useNavigate();

  //   
  const [nickName, setNickName] = useState("");
  const [nameCheck, setNameCheck] = useState(false);
  const nameCheckFn = (e) => {
    e.preventDefault();
    //   
    if (!nickName) {
      return alert(" .");
    }
    //   UserModel     
    const body = {
      displayName: nickName,
    };
    axios
      .post("/api/user/namecheck", body)
      .then((response) => {
        //    
        if (response.data.success) {
          if (response.data.check) {
            // 
            //    
            setNameCheck(true);
            alert(" .");
          } else {
            //  
            setNameCheck(false);
            alert("  .");
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  //  
  const nameUpdateFn = (e) => {
    e.preventDefault();
    if (!nickName) {
      return alert(" .");
    }
    //   
    if (!nameCheck) {
      return alert("  .");
    }
    // firebase     
    fireUser.currentUser
      .updateProfile({
        displayName: nickName,
      })
      .then(() => {
        alert(" .");
        let body = {
          email: email,
          displayName: nickName,
          uid: user.uid,
        };
        axios
          .post("/api/user/update", body)
          .then((response) => {
            //    .
            //   dipatch   
            //     userSlice state  
            if (response.data.success) {
              alert("  .");
              const userInfo = {
                displayName: nickName,
                uid: user.uid,
                accessToken: user.accessToken,
                email: email,
              };
              dispatch(loginUser(userInfo));
              setNickName(nickName);
            } else {
              alert("  .");
            }
          })
          .catch((error) => {
            // alert("  .");
            console.log(error);
          });
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode, errorMessage);
        alert("  .\n  .");
        firebase.auth().signOut();
        dispatch(clearUser());
        navigate("/login");
      });
  };
  //  
  const emailUpdateFn = (e) => {
    e.preventDefault();
    //   
    if (!email) {
      return alert(" .");
    }

    // firebase   
    fireUser.currentUser
      .updateEmail(email)
      .then(() => {
        let body = {
          email: email,
          displayName: nickName,
          uid: user.uid,
        };
        axios
          .post("/api/user/update", body)
          .then((response) => {
            //    .
            //   dipatch   
            //     userSlice state  
            if (response.data.success) {
              alert("  .");
              const userInfo = {
                displayName: nickName,
                uid: user.uid,
                accessToken: user.accessToken,
                email: email,
              };
              dispatch(loginUser(userInfo));
              setEmail(email);
            } else {
              alert("  .");
            }
          })
          .catch((error) => {
            // alert("  .");
            console.log(error);
          });
      })
      .catch((error) => {
        //  
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode, errorMessage);
        alert("  .\n  .");
        firebase.auth().signOut();
        dispatch(clearUser());
        navigate("/login");
      });
  };

  //  
  const passUpdateFn = (e) => {
    e.preventDefault();
    if (!pw) {
      return alert(" .");
    }
    if (!pwCheck) {
      return alert("  .");
    }
    //   
    if (pw !== pwCheck) {
      return alert("  .");
    }
    // firebase  
    fireUser.currentUser
      .updatePassword(pw)
      .then(() => {
        alert(" .");
        setPw(pw);
        setPwCheck("");
      })
      .catch((error) => {
        //  
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode, errorMessage);
        alert("  .\n  .");
        firebase.auth().signOut();
        dispatch(clearUser());
        navigate("/login");
      });
  };
  //  
  const registOutFunc = (e) => {
    e.preventDefault();
    // firebase  
    fireUser.currentUser
      .delete()
      .then(() => {
        //       
        //   
        let body = {
          uid: user.uid,
        };
        axios
          .post("/api/post/userout", body)
          .then((response) => {
            if (response.data.success) {
              alert(" .");
              //   
              dispatch(clearUser());
              navigate("/login");
            } else {
              //   
              console.log("     .");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode, errorMessage);
      });
  };

  return (
    <div className="p-6 m-4 shadow">
      <h2>User Info</h2>
      <SignUpDiv>
        <form>
          <div className="flex justify-start mb-3">
            <label className="mr-5 text-xl "></label>
            <input
              type="text"
              className="mr-5"
              required
              maxLength={20}
              minLength={3}
              value={nickName}
              onChange={(e) => setNickName(e.target.value)}
            />
            <button className="mr-5" onClick={(e) => nameCheckFn(e)}>
               
            </button>
            <button onClick={(e) => nameUpdateFn(e)}> </button>
          </div>
        </form>

        <form>
          <div className="flex justify-start mb-3">
            <label className="mr-5 text-xl"></label>
            <input
              type="email"
              className="mr-5"
              required
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <button onClick={(e) => emailUpdateFn(e)}> </button>
          </div>
        </form>
        <form>
          <div className="mb-3">
            <div className=" text-xl font-bold mb-3"> </div>
            <div className="flex justify-start mb-3">
              <label className="mr-5 text-sm items-center "></label>
              <input
                type="password"
                className="mr-5"
                required
                maxLength={16}
                minLength={6}
                value={pw}
                onChange={(e) => setPw(e.target.value)}
              />
              <label className="mr-5 text-sm items-center ">
                 
              </label>
              <input
                type="password"
                className="mr-5"
                required
                maxLength={16}
                minLength={6}
                value={pwCheck}
                onChange={(e) => setPwCheck(e.target.value)}
              />

              <button disabled={btFlag} onClick={(e) => passUpdateFn(e)}>
                 
              </button>
            </div>
          </div>
        </form>

        <div className="flex justify-start">
          <button disabled={btFlag} onClick={(e) => registOutFunc(e)}>
            
          </button>
        </div>
      </SignUpDiv>
    </div>
  );
};

export default UserInfo;
