import React, { useCallback, useState, useEffect } from "react";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";
// import {Dropdown,DropdownButton} from 'react-bootstrap';
// import Spinner from "react-bootstrap/Spinner";

// 1.  
import { useSelector } from "react-redux";
import { useNavigate } from "react-router";
import axios from "axios";
import Form from "../components/Form";
import List from "../components/List";
// import { Navigate } from "react-router";
// import Loading from "../components/Loading";
import LoadingSpinner from "../components/LoadingSpinner";

/*
  / ( 2 )
    ,  

      class : TypeScript
  state  (Re-rendering)
  Life-cycle : Mounte, Update, unMount...

      function
  state     .
  useState() state 
  -------------------------
  Life-cycle   .
  useEffect() Life-cycle 

 */

/*
    todoData  
  todoData  useState    .
  useState()
  :   .
 */
//   
// mongeDB  
// let initTodo = localStorage.getItem("todoData");
// initTodo = initTodo ? JSON.parse(initTodo) : [];

const Todo = () => {
  //   

  // const [todoData, setTodoData] = useState(initTodo);
  const [todoData, setTodoData] = useState([]);
  const [todoValue, setTodoValue] = useState("");

  // 2.   
  const navigate = useNavigate();

  const user = useSelector((state) => state.user);
  console.log("user", user);
  useEffect(() => {
    //    
    if (user.accessToken === "") {
      //   
      alert("  .");
      navigate("/login");
    } else {
      //   
    }
  }, [user]);
  //   
  const [sort, setSort] = useState("");
  useEffect(() => {
    setSkip(0);
    getList(search, 0);
  }, [sort]);

  //  
  const [search, setSearch] = useState("");
  const searchHandler = () => {
    setSkip(0);
    getList(search, 0);
  };

  // axios   API
  //   
  const getList = (_word = "", _stIndex = 0) => {
    setSkip(0);
    //  
    setLoading(true);
    //    
    setSkipToggle(false);
    const body = {
      sort: sort,
      search: _word,
      //  
      uid: user.uid,
      skip: _stIndex,
    };
    axios
      .post("/api/post/list", body)
      .then((response) => {
        // console.log(response.data);
        //   
        if (response.data.success) {
          setTodoData(response.data.initTodo);
          //  skip  .
          setSkip(response.data.initTodo.length);
          // console.log(response.data.total);

          //  DB      
          // .
          if (response.data.total > 5) {
            setSkipToggle(true);
          }
        }
        //  
        setLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const getListGo = (_word = "", _stIndex = 0) => {
    setLoading(true);
    let body = {
      sort: sort,
      search: _word,
      //  
      uid: user.uid,
      skip: _stIndex,
    };
    axios
      .post("/api/post/list", body)
      .then((response) => {
        // console.log(response.data);
        if (response.data.success) {
          const newArr = response.data.initTodo;
          setTodoData([...todoData, ...newArr]);
          //  slip 
          setSkip(skip + newArr.length);
          if (newArr.length < 5) {
            setSkipToggle(false);
          }
        }

        //  
        setLoading(false);
      })

      .catch((error) => {
        console.log(error);
      });
  };

  //   
  const [skip, setSkip] = useState(0);
  const [skipToggle, setSkipToggle] = useState(true);

  const getListMore = () => {
    getListGo(search, skip);
  };

  useEffect(() => {
    getList("", skip);
    //      
  }, []);

  const deleteClick = useCallback(
    (id) => {
      if (window.confirm(" ?")) {
        let body = {
          id: id,
        };
        setLoading(true);
        axios
          .post("/api/post/delete", body)
          .then((response) => {
            console.log(response);
            const nowTodo = todoData.filter((item) => item.id !== id);
            setTodoData(nowTodo);
            setLoading(false);
          })
          .catch((error) => {
            console.log(error);
          });
      }
      //  ID       

      // console.log("", nowTodo);
      //  .
      // axios  MongoDB

      //  .(DB )
      // localStorage.setItem("todoData", JSON.stringify(nowTodo));
    },
    [todoData]
  );

  const addTodoSubmit = (event) => {
    //      .
    event.preventDefault();
    //    
    let str = todoValue;
    str = str.replace(/^\s+|\s+$/gm, "");
    if (str.length === 0) {
      alert(" .");
      setTodoValue("");
      return;
    }
    // { id: 4, title: " 4", completed: false }
    // todoData      
    // {}  ..
    //  .map    jsx    .
    const addTodo = {
      id: Date.now(), // id  .map key  , unique 
      title: todoValue, //    
      completed: false, //       false 
      // 1. DB:server/Model/TodoModel Schema(objectID)
      //  uid   

      uid: user.uid,
    };

    setLoading(true);
    //     ,    
    //    Destructuring   
    // todoData: [{},{},{},{},    {}]     [{}]
    // axios MongoDb  
    axios
      .post("/api/post/submit", { ...addTodo })
      .then((res) => {
        // console.log(res.data);
        if (res.data.success) {
          //     
          setTodoValue("");
          getList("", 0);
          alert("  ");
        } else {
          alert("   ");
        }
      })
      .catch(() => {
        console.log();
      });
  };

  const deleteAllClick = () => {
    if (window.confirm("  ?")) {
      // axios MongoDb  

      setLoading(true);
      axios
        .post("/api/post/deleteall")
        .then(() => {
          setTodoData([]);
          setSkip(0);
          setLoading(false);
        })

        .catch((error) => console.log(error));
    }
    //  .(DB )
    //  .(DB )
    // localStorage.clear();
  };

  // 
  const [loading, setLoading] = useState(false);
  // const loadingCSS = {
  //   position: "fixed",
  //   left: 0,
  //   top: 0,
  //   display: "flex",
  //   justifyContent: "center",
  //   alignItems: "center",
  //   width: "100vw",
  //   height: "100vh",
  //   backgroundColor: "rgba(0,0,0,0.5)",
  // };

  return (
    <div className="flex justify-center w-full ">
      <div className="w-full p-6 m-4 bg-white shadow ">
        <div className="flex justify-between mb-3">
          <h1> </h1>
          <button onClick={deleteAllClick}>Delete All</button>
        </div>

        <div className="flex justify-between mb-3">
          <DropdownButton title={sort} variant="outline-secondary">
            <Dropdown.Item onClick={() => setSort("")}>
              
            </Dropdown.Item>
            <Dropdown.Item onClick={() => setSort("")}>
              
            </Dropdown.Item>
          </DropdownButton>

          <div>
            <label className="mr-2"> </label>
            <input
              placeholder=" "
              type="text"
              className="border-2"
              value={search}
              onChange={(e) => setSearch(e.currentTarget.value)}
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  searchHandler();
                }
              }}
            />
          </div>
        </div>

        <List
          todoData={todoData}
          setTodoData={setTodoData}
          deleteClick={deleteClick}
        />

        {skipToggle && (
          <div className="flex justify-center">
            <button
              className="p-2 text-blue-400 border-2
            border-blue-400 rounded
            hover:text-white
            hover:bg-blue-400"
              onClick={() => getListMore()}
            >
              
            </button>
          </div>
        )}

        <Form
          todoValue={todoValue}
          setTodoValue={setTodoValue}
          addTodoSubmit={addTodoSubmit}
        />
      </div>

      {/*   */}
      {loading && <LoadingSpinner />}
    </div>
  );
};

export default Todo;
