import axios from "axios";
import React, { useState } from "react";

const ListItem = React.memo(({ item, todoData, setTodoData, deleteClick }) => {
  //   console.log("listItem Rendering...");
  //       State
  // isEditing false  
  // isEditing ture  
  const [isEditing, setIsEditing] = useState(false);
  //   //      State
  //      
  const [editedTitle, setEditedTitle] = useState(item.title);

  // console.log(item);
  //   const deleteClick = (id) => {
  //     //       
  //     const nowTodo = todoData.filter((item) => item.id !== id);
  //     console.log("", nowTodo);
  //     setTodoData(nowTodo);
  //   };
  //      
  const editChange = (event) => {
    setEditedTitle(event.target.value);
  };

  const toggleClick = (id) => {
    // map  this.state.todoData complete 
    const updateTodo = todoData.map((item) => {
      if (item.id === id) {
        //   
        // !   
        item.completed = !item.completed;
      }
      return item;
    });

    let body = {
      id: todoId,
      completed: item.completed,
    };
    // axios  MongoDb complete
    // then(): ()  
    // catch():  
    axios
      .post("/api/post/updatetoggle", body)
      .then((response) => {
        // console.log(response);
        setTodoData(updateTodo);
      })
      .catch((error) => {
        console.log(error);
      });
    //  .(DB )
    // localStorage.setItem("todoData", JSON.stringify(updateTodo));
  };

  //  item.id   
  const todoId = item.id;
  const updateTitle = () => {
    //    
    let str = editedTitle;
    str = str.replace(/^\s+|\s+$/gm, "");
    if (str.length === 0) {
      alert(" .");
      setEditedTitle(item.title);
      return;
    }

    let tempTodo = todoData.map((item) => {
      //  todoData   ID  
      if (item.id === todoId) {
        //   .
        item.title = editedTitle;
      }
      return item;
    });
    //  
    // axios   MongoDB 
    let body = {
      id: todoId,
      title: editedTitle,
    };
    axios
      .post("/api/post/updatetitle", body)
      .then((response) => {
        //   
        console.log(response.data);
        setTodoData(tempTodo);
        //  
        setIsEditing(false);
      })
      .catch((error) => {
        console.log(error);
      });
    //  (DB )
    // localStorage.setItem("todoData", JSON.stringify(tempTodo));
  };

  // 
  const WEEKDAY = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];const showTime = (_timestamp) => {
    const date = new Date(_timestamp);
    //  ,  
    let hours = date.getHours();
    let ampm = hours >= 12 ? "PM" : "AM";
    hours = hours % 12;
    hours = hours ? hours : 12; // the hour '0' should be '12'
    hours = hours + 1 < 9 ? "0" + hours : hours;

    //  
    let minutes = date.getMinutes();
    minutes = minutes < 10 ? "0" + minutes : minutes;

    //  
    let seconds = date.getSeconds();
    seconds = seconds < 10 ? "0" + seconds : seconds;
    // 
    let months = date.getMonth();
    months = months + 1 < 9 ? "0" + (months + 1) : months + 1;

    let time = date.getFullYear();
    time += "/";
    time += months;
    time += "/";
    time += date.getDate();
    // time += "/";
    // time += WEEKDAY[date.getDay()];
    time += "  ";
    time += hours;
    time += ":";
    time += minutes;
    time += ":";
    time += seconds;
    time += "  ";
    time += ampm;

    return time;
  };

  if (isEditing) {
    //  jsx 
    return (
      <div className="flex items-center justify-between w-full px-4 py-1 my-2 text-gray-600 bg-gray-100 border rounded">
        <div className="items-center ">
          <input
            type="text"
            className="w-full px-3 py-2 mr-4 text-gray-500 bg-white border rounded"
            value={editedTitle}
            onChange={editChange}
          />
        </div>
        <div className="items-center">
          <button className="px-4 py-2" onClick={updateTitle}>
            Update
          </button>
          <button className="px-4 py-2" onClick={() => setIsEditing(false)}>
            {" "}
            Close{" "}
          </button>
        </div>
      </div>
    );
  } else {
    //  jsx 
    return (
      <div className="flex items-center justify-between w-full px-4 py-1 my-2 text-gray-600 bg-gray-100 border rounded">
        <div className="items-center ">
          <input
            type="checkbox"
            defaultChecked={item.completed}
            onChange={() => toggleClick(item.id)}
          />{" "}
          <span className={item.completed ? "line-through" : "none"}>
            {item.title}
          </span>
        </div>
        <div className="items-center">
          <span>{showTime(item.id)}</span>
          <button
            className="px-4 py-2"
            onClick={() => {
              setIsEditing(true);
              setEditedTitle(item.title);
            }}
          >
            Edit
          </button>
          <button className="px-4 py-2" onClick={() => deleteClick(item.id)}>
            x
          </button>
        </div>
      </div>
    );
  }
});

export default ListItem;
