import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import SignUpDiv from "../styles/SignUpCss";
// firebase   
import firebase from "../firebase";
import axios from "axios";

const SignUp = () => {
  const [nickName, setNickName] = useState("");
  const [email, setEemail] = useState("");
  const [pw, setPw] = useState("");
  const [pwChek, setPwChek] = useState("");
  const [btFlag, setBtFlag] = useState("");
  const navigate = useNavigate();

  // firebase  
  const registFunc = (e) => {
    e.preventDefault();
    if (!nickName) {
      setBtFlag(false);
      return alert(" .");
    }
    if (!email) {
      return alert(" .");
    }
    if (!pw) {
      return alert(" .");
    }
    if (!pwChek) {
      return alert("  .");
    }
    //   
    if (pw !== pwChek) {
      return alert("  .");
    }
    //3.  
    if (!nameCheck) {
      return alert("  ");
    }
    //  
    setBtFlag(true);
    // firebase    
    // https://firebase.google.com/docs/auth/web/start?hl=ko&authuser=3#web-version-9_1
    const createUser = firebase.auth();
    createUser
      .createUserWithEmailAndPassword(email, pw)
      .then((userCredential) => {
        // Signed in
        const user = userCredential.user;
        // console.log(user);
        //   
        // https://firebase.google.com/docs/auth/web/manage-users
        user
          .updateProfile({ displayName: nickName })
          .then(() => {
            //   
            //   (,,UID)
            console.log(user);
            let body = {
              email: user.email,
              displayName: user.displayName,
              uid: user.uid,
            };
            axios
              .post("/api/user/register", body)
              .then((response) => {
                console.log(response.data);
                if (response.data.success) {
                  firebase.auth().signOut();
                  //   
                  navigate("/login");
                } else {
                  //   
                  console.log("     ");
                }
              })
              .catch((err) => {
                console.log(err);
              });
          })
          .catch((error) => {
            //   
          });
      })
      .catch((error) => {
        //  
        setBtFlag(false);
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode, errorMessage);
      });
  };
  // 2. 
  const [nameCheck, setNameCheck] = useState(false);
  const nameCheckFn = (e) => {
    e.preventDefault();
    if (!nickName) {
      return alert(" ");
    }
    //   Usermodel    

    const body = {
      displayName: nickName,
    };
    axios
      .post("/api/user/namecheck", body)
      .then((response) => {
        if (response.data.success) {
          if (response.data.check) {
            // 
            // 
            setNameCheck(true);
            alert(" ");
          } else {
            // 
            setNameCheck(false);
            alert("  ");
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <div className="p-6 m-4 shadow">
      <h2>SignUp</h2>
      <SignUpDiv>
        <form>
          <label></label>
          <input
            type="text"
            required
            value={nickName}
            maxLength={20}
            minLength={3}
            onChange={(e) => setNickName(e.target.value)}
          />
          <button onClick={(e) => nameCheckFn(e)}> </button>

          <label></label>
          <input
            type="email"
            required
            value={email}
            onChange={(e) => setEemail(e.target.value)}
          />

          <label></label>
          <input
            type="password"
            required
            value={pw}
            maxLength={16}
            minLength={6}
            onChange={(e) => setPw(e.target.value)}
          />
          <label> </label>
          <input
            type="password"
            required
            value={pwChek}
            maxLength={16}
            minLength={6}
            onChange={(e) => setPwChek(e.target.value)}
          />
          <button disabled={btFlag} onClick={(e) => registFunc(e)}>
            
          </button>
        </form>
      </SignUpDiv>
    </div>
  );
};

export default SignUp;
